.navigation-bar{position:fixed;top:0;left:0;width:100%;height:max-content;z-index:10}.navigation-bar .toolbar-container{position:relative;width:100%;height:6.8rem;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;background-color:var(--navbar-bg-color);padding:0 2.8rem;border-bottom:1px solid var(--navbar-divider-color)}.navigation-bar .toolbar-container .toolbar__nav-link-container{display:flex;flex-direction:row;align-items:stretch;justify-content:flex-start;height:100%;width:max-content;flex-shrink:0}.navigation-bar .toolbar-container .toolbar__item{display:block;flex-shrink:0;width:max-content;height:auto}.navigation-bar .toolbar-container .toolbar__item.logo{margin-right:3.2rem}.navigation-bar .toolbar-container .toolbar__item.logo a{position:relative;display:inline-block;cursor:pointer}.navigation-bar .toolbar-container .toolbar__item.logo .toolbar__logo-image{display:block;height:calc(6.8rem * 0.85);width:auto}